import PropTypes from 'prop-types';

import React, { Component } from 'react';

class Order extends React.Component {
  static PropTypes = {
    id: PropTypes.string.isRequired,
  };
  render() {
    return (
      <div>
        <p>Order ID: {this.props.order.id}</p>
      </div>
    );
  }
}

export default Order;
